.menu-icon{position:absolute;width:30px;height:2px;bottom:15px;background-color:#fff}.menu-icon::before{position:absolute;left:0;top:-10px;content:"";display:block;width:30px;height:2px;background-color:#fff;transition:transform 0.2s ease-in, top 0.2s linear 0.2s}.menu-icon::after{position:absolute;left:0;top:10px;content:"";display:block;width:30px;height:2px;background-color:#fff;transition:transform 0.2s ease-in, top 0.2s linear 0.2s}.menu-icon.menu-icon-active{background-color:transparent;z-index:1}.menu-icon.menu-icon-active::before{transform:rotate(45deg);top:0;transition:top 0.2s linear, transform 0.2s ease-in 0.2s}.menu-icon.menu-icon-active::after{transform:rotate(-45deg);top:0;transition:top 0.2s linear, transform 0.2s ease-in 0.2s}.burger-menu-wrapper{width:30px;height:30px;position:absolute;top:40px;right:20px}

.burgerpage{max-width:100%;position:absolute;background-color:#737373;right:0;left:0}.burgerpage-links{display:flex;flex-flow:wrap;flex-flow:column;justify-content:center;text-align:center;align-items:center}.burgerpage-links-item{font-weight:normal;font-size:23px;line-height:27px;color:#ffffff;text-decoration:none;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin-bottom:40px}.burgerpage-links-item:first-child{margin-top:247px}.burgerpage-links-item:last-child{margin-bottom:279px}.burgerpage-footer-text{font-weight:300;font-size:15px;line-height:15px;text-align:center;color:#ffffff;margin-bottom:31px}.burgerpage-footer-text:last-child{margin-bottom:39px}

