@-webkit-keyframes tooltip-opacity{from{opacity:0}to{opacity:1}}@keyframes tooltip-opacity{from{opacity:0}to{opacity:1}}.policy-item{position:relative;width:calc((100% / 3) - 94px);height:209px;width:341px;margin-bottom:30px;display:flex;align-items:center}.policy-item-tooltip{position:absolute;pointer-events:none;-webkit-appearance:none;-moz-appearance:none;appearance:none;left:100%;width:400px;padding:16px 16px 16px 75px;background-color:#dadada;-webkit-clip-path:polygon(100% 0, 100% 100%, 75px 100%, 0 50%, 75px 0);clip-path:polygon(100% 0, 100% 100%, 75px 100%, 0 50%, 75px 0);z-index:1;-webkit-animation:tooltip-opacity 0.3s ease-in-out;animation:tooltip-opacity 0.3s ease-in-out}.policy-item-tooltip-switched{-webkit-clip-path:polygon(calc(100% - 75px) 0, 100% 50%, calc(100% - 75px) 100%, 0 100%, 0 0);clip-path:polygon(calc(100% - 75px) 0, 100% 50%, calc(100% - 75px) 100%, 0 100%, 0 0);left:auto;left:initial;right:100%;padding:16px 75px 16px 16px}.policy-item-name{width:100%;text-align:center;font-weight:normal;font-size:23px;line-height:30px;background:rgba(255,255,255,0.83);color:#222222}.policy-item-mobile-tooltip{display:block;position:absolute;width:100%;height:auto;background:rgba(255,255,255,0.83);z-index:1;text-align:left;font-weight:normal;font-size:19px;line-height:22px;color:#000000;padding-left:20px}.policy-item-mobile-tooltip-title{font-weight:normal;font-size:23px;line-height:27px;text-align:center;color:#222222;margin-bottom:39px;margin-top:70px}@media (max-width: 1340px){.policy-item-tooltip{max-width:250px}.policy-item-tooltip-switched{right:100%;left:auto;left:initial}}@media (max-width: 700px){.policy-item-tooltip{padding:16px 16px 16px 16px;-webkit-clip-path:polygon(100% 0, 100% 95%, 51% 100%, 0 95%, 0 0);clip-path:polygon(100% 0, 100% 95%, 51% 100%, 0 95%, 0 0);left:0;right:0}}

.footer{border-top:1px solid #ffffff}.footer-partners{display:flex;align-items:center}.footer-partners-item{margin-right:120px}.footer-partners-item:last-child{margin-right:0px}.footer-partners-item img{width:100%;height:auto}.footer-text-wrap{display:flex;justify-content:space-between}.footer-nav{display:flex;flex-direction:column;margin-bottom:88px}.footer-nav-link{font-weight:normal;font-size:17px;line-height:20px;color:#ffffff;text-decoration:none;margin-bottom:20px}.footer-nav-link:last-child{margin-bottom:0px}.footer-nav-text{width:366px;font-weight:normal;font-size:17px;line-height:20px;color:#ffffff}.footer-text{font-weight:300;font-size:14px;line-height:14px;color:#ffffff;margin-bottom:14px}.text-yellow{color:#eee851}@media (max-width: 720px){.footer-partners{margin-bottom:41px}.footer-partners-item{margin-right:80px}.footer-partners-item:last-child{margin-right:20px}.footer-text-wrap{display:flex;flex-direction:column}.footer-nav{display:flex;flex-direction:column;margin-bottom:112px}.footer-nav-link{font-weight:normal;font-size:15px;line-height:18px;text-align:center;color:#ffffff;text-decoration:none;margin-bottom:20px}.footer-nav-link:last-child{margin-bottom:0px}.footer-nav-text{margin:0px 20px 88px 20px;font-weight:normal;font-size:15px;line-height:18px;text-align:center;color:#ffffff;width:auto;min-width:0}.footer-text{font-weight:300;font-size:10px;line-height:10px;text-align:center;margin-bottom:10px}}

